.sticky-nav {
    grid-area: nav;
    display: none;
    background: #fff;
    border: 0px solid rgba(51, 51, 51, 0.06);
    border-radius: 16px;
    box-shadow: 0 0 8px 0 rgba(51, 51, 51, 0.2);
    overflow: hidden;
    width: 250px;
    padding: 35px 30px 15px 18px;
    margin-top: 30px;
  
  }